export default "import { NgModule } from \"@angular/core\";\nimport { RouterModule } from \"@angular/router\";\nimport {\n    DEMO_PATH_TOKEN,\n    NuiBusyModule,\n    NuiButtonModule,\n    NuiCheckboxModule,\n    NuiDocsModule,\n    NuiMessageModule,\n    NuiProgressModule,\n    NuiSelectModule,\n    NuiSpinnerModule,\n    SrlcStage,\n} from \"@nova-ui/bits\";\n\nimport { BusyDocsExampleComponent } from \"./busy-docs/busy-docs.example.component\";\nimport {\n    BusyBasicExampleComponent,\n    BusyProgressExampleComponent,\n    BusySpinnerExampleComponent,\n    BusyTestComponent,\n    BusyVisualTestComponent,\n} from \"./index\";\n\nconst routes = [\n    {\n        path: \"\",\n        component: BusyDocsExampleComponent,\n        data: {\n            \"srlc\": {\n                \"stage\": SrlcStage.ga,\n            },\n            showThemeSwitcher: true,\n        },\n    },\n    {\n        path: \"busy-visual-test\",\n        component: BusyVisualTestComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n    {\n        path: \"busy-test\",\n        component: BusyTestComponent,\n        data: {\n            \"srlc\": {\n                \"hideIndicator\": true,\n            },\n        },\n    },\n];\n\n@NgModule({\n    imports: [\n        NuiBusyModule,\n        NuiButtonModule,\n        NuiMessageModule,\n        NuiSpinnerModule,\n        NuiCheckboxModule,\n        NuiSelectModule,\n        NuiProgressModule,\n        NuiDocsModule,\n        RouterModule.forChild(routes),\n    ],\n    declarations: [\n        BusyBasicExampleComponent,\n        BusyDocsExampleComponent,\n        BusySpinnerExampleComponent,\n        BusyProgressExampleComponent,\n        BusyTestComponent,\n        BusyVisualTestComponent,\n    ],\n    providers: [\n        {\n            provide: DEMO_PATH_TOKEN,\n            useFactory: () => (<any>require).context(`!!raw-loader!./`, true, /.*\\.(ts|html|less)$/),\n        },\n    ],\n    exports: [\n        RouterModule,\n    ],\n})\nexport class BusyModule {\n}\n";